@font-face {
  font-family: themeFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: headerFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: postTitleFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: postContentFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: navLinkFont;
  src: url('ptsans/PTC75F.ttf');
}

* {
  box-sizing: border-box;
}

html {
    margin: 0px;
    padding: 0px;
}

body {
 	margin: 0px;
  padding: 0px;
	font-family: postTitleFont;
	background-color: #eeeeee;
	background-repeat: no-repeat;
	color: #313233
}

.navbar {
	overflow: auto;
	height: 44px;
	width: 100%;
	box-sizing:border-box;
	box-shadow: 0px 0px 8px 1px #222222;
	background-color: #d4d6d9;
}

.current {
 	color: #a03a2b;
}

.uncurrent {
	color: #313233;
}

#navlink {
	float: right;
	margin-top: 13px;
	margin-right: 17px;
	font-family: navLinkFont;
	text-shadow: 0px 0px #999999;
}

h1 {
	margin: auto;
	color: #111111;
}

.postTitle {
		font-family: postTitleFont;
		color: #a03a2b
}

.postContent {
		font-family: postContentFont;
}

.headerbar {
	height: 94px;
	width: 100%;
	box-sizing:border-box;
	overflow: hidden;
	color: #000000;
	text-shadow: 2px 2px #7e8bab;
	font-family: headerFont;
	position: relative;
	font-size: large;
}

.headerverticalcenter {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.leftspacedtitle {
	margin-top: 0px;
	margin-bottom: 10px;
   	padding: 10px;
   	font-family: headerFont;
}

.separator-basic {
	height: 2px;
	width: 100%;
	margin: 0px;
	padding: 0px;
	background-color: #777777;
}

a {
	color: #a05b3c;
	text-decoration: none;
}

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"] {
  float: left;
  padding: 15px;
  margin: 5px;
  overflow: hidden;
  border: 1px solid #999999;
}

.savedextra {
	  background-color: #63a583;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

.sidebarcat {
	background-color: #d4d6d9;
	padding: 15px;
	font-family: navLinkFont;
}

.maincontent {
	box-shadow: 3px 6px #999999;
}

footer {
  text-align: center;
  margin-top: 50px;
  padding: 3px;
  background-color: #777777;
  color: white;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 98%;
  };

  .headerbar {
  	font-size: 15px;
  }
}